@import url(https://fonts.googleapis.com/css2?family=Asul&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 70px;
    padding-left: 10px;
    background-color: #f1f1f1;
    position: fixed;
    height: 100%;
    overflow: auto;
}

.sidebar a {
    display: block;
    color: black;
    padding: 16px;
    text-decoration: none;
}

@media screen and (max-width: 700px) {
    .sidebar {
        flex-direction: row;
        width: 100%;
        height: auto;
        position: relative;
    }
    .sidebar a {
        float: left;
    }
}

@media screen and(max-width: 400px) {
    .sidebar a {
        text-align: center;
        float: none;
    }
}

.avatar {
    height: 45vh;
    margin-top: 15%;
    border-radius: 4px;
    vertical-align: top;
}

.figure {
    display: inline-block;
    margin-left: 15%;
}

@media screen and (max-width: 540px) {
    .avatar {
        height: 35vh;
    }
}

.textContainer {
    margin-left: 25%;
    margin-right: 10%;
}

.sectionContainer {
    display: flex;
    margin-top: 20px;
}

.textStyle {
    font-family: 'Asul';
    text-align-last: left;
    font-size: 18px;
}

@media screen and (max-width: 700px) {
    .textContainer {
        margin-left: 10%;
        margin-right: 10%;
    }

    .sectionContainer {
        margin-top: 5px;
    }
}

@media screen and (max-width: 540px) {
    .textContainer {
        margin-left: 15%;
    }

    .sectionContainer {
        padding-top: 0;
        margin-top: 0;
    }
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}

html {
    overflow-x: hidden;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

